export function getMergeSortAnimations(array) {
  const animations = [];
  if (array.length <= 1) return array;
  const secondaryArray = array.slice();
  mergeSortHelper(array, 0, array.length - 1, secondaryArray, animations);
  return animations;
}

function mergeSortHelper(
  mainArray,
  startIdx,
  endIdx,
  secondaryArray,
  animations,
) {
  if (startIdx === endIdx) return;
  const middleIdx = Math.floor((startIdx + endIdx) / 2);
  mergeSortHelper(secondaryArray, startIdx, middleIdx, mainArray, animations);
  mergeSortHelper(secondaryArray, middleIdx + 1, endIdx, mainArray, animations);
  doMerge(mainArray, startIdx, middleIdx, endIdx, secondaryArray, animations);
}

function doMerge(
  mainArray,
  startIdx,
  middleIdx,
  endIdx,
  secondaryArray,
  animations,
) {
  let k = startIdx;
  let i = startIdx;
  let j = middleIdx + 1;
  while (i <= middleIdx && j <= endIdx) {
    animations.push([i, j]);
    animations.push([i, j]);
    if (secondaryArray[i] <= secondaryArray[j]) {
      animations.push([k, secondaryArray[i]]);
      mainArray[k++] = secondaryArray[i++];
    } else {
      animations.push([k, secondaryArray[j]]);
      mainArray[k++] = secondaryArray[j++];
    }
  }
  while (i <= middleIdx) {
    animations.push([i, i]);
    animations.push([i, i]);
    animations.push([k, secondaryArray[i]]);
    mainArray[k++] = secondaryArray[i++];
  }
  while (j <= endIdx) {
    animations.push([j, j]);
    animations.push([j, j]);
    animations.push([k, secondaryArray[j]]);
    mainArray[k++] = secondaryArray[j++];
  }
}



