import React from 'react';
import {getMergeSortAnimations} from '../SortingAlgorithms/Sorting.js';
import './SortingVisualizer.css';

const SORTSPEED = 3;

const BASECOLOR = 'orange';

const SECONDARYCOLOR = 'black';

export default class SortingVisualizer extends React.Component{
  constructor(props){
    super(props);

    this.state = {
      array: [],
    };
  }

  componentDidMount() {
    this.shuffleArray();
  }

  shuffleArray() {
    const array = [];
    for(let i = 0; i < 250; i++){
      array.push(randomIntFromIntervals(5,675));
    }
    this.setState({array});
  }

  mergeSort() {
    const animations = getMergeSortAnimations(this.state.array);
    for (let i = 0; i < animations.length; i++) {
      const arrayBars = document.getElementsByClassName('array-bar');
      const isColorChange = i % 3 !== 2;
      if (isColorChange) {
        const [barOneIdx, barTwoIdx] = animations[i];
        const barOneStyle = arrayBars[barOneIdx].style;
        const barTwoStyle = arrayBars[barTwoIdx].style;
        const color = i % 3 === 0 ? SECONDARYCOLOR : BASECOLOR;
        setTimeout(() => {
          barOneStyle.backgroundColor = color;
          barTwoStyle.backgroundColor = color;
        }, i * SORTSPEED);
      } else {
        setTimeout(() => {
          const [barOneIdx, newHeight] = animations[i];
          const barOneStyle = arrayBars[barOneIdx].style;
          barOneStyle.height = `${newHeight}px`;
        }, i * SORTSPEED);
      }
    }
    return (
      <div className="array-bar"
        style={{backgroundColor: 'purple'}}></div>
    )
  }

  render(){
    const {array} = this.state;

    return (
      <div className="array-container">
        {array.map((value,index) => (
            <div className="array-bar"
              key={index}
              style={{
                backgroundColor: BASECOLOR,
                height: `${value}px`
              }}>
            </div>
          ))}
          <button className="generate" onClick={() => this.shuffleArray()}>Generate New Array</button>
          <button className="mergeSortButton" onClick={() => this.mergeSort()}>Merge Sort</button>
      </div>
    );
  }
}

function randomIntFromIntervals(min,max) {
  return Math.floor(Math.random() * (max - min + 1) + min);
}



